.card {
  margin-top: 30px;
  border: 20px solid #363944;
  background: #BF8737;
  transition: transform 0.15s ease-out;

  &:hover {
    transform: scale(1.2);
    box-shadow: -1px 2px 185px 51px rgba(0,0,0,0.50); } }


.name {
  font-size: 15px;
  border-top: 5px solid rgba(255,255,255,.7);
  border-left: 5px solid rgba(255,255,255,.7);
  border-right: 5px solid rgba(0,0,0,.5);
  border-bottom: 5px solid rgba(0,0,0,.5);
  margin: 15px 15px;
  padding: 8px; }

.level {
  text-align: right;
  color: yellow;
  margin: 10px 40px; }

.image {
  margin: 10px 40px;
  img {
    border: 8px solid #545F7C;
    box-shadow: 5px 5px 5px rgba(0,0,0,.2);
    width: 98%; } }

.description {
  margin: 10px;
  background: #DDCCB3;
  padding: 6px;
  border: 4px solid #B34930;
  max-height: 180px;

  h2 {
    font-variant: small-caps;
    margin: 0;
    font-style: normal; }

  p {
    font-style: italic;
    font-size: 12px;
    margin: 0; } }

.stats {
  margin-top: 10px;
  border-top: 2px solid #000;
  text-align: right;

  .stats p {
    margin-top: -21px;
    text-align: left; } }
